.ba-services {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  min-height: 800px;
  visibility: hidden;
}

.ba-services--visible {
  visibility: visible;
}

.ba-services__items {
  width: 760px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.ba-services__item {
  max-width: 380px;
  flex: 1 1 380px;
  height: 400px;
  text-align: center;
  padding: 100px 10px 10px;
  transition: transform 0.25s;
}

.ba-services__item-design, .ba-services__item-mobile {
  background-color: #dbdbdb;
}

.ba-services__item-icon {
  margin: 0 auto;
}

.ba-services__item-icon-design {
  background: url("../img/icon-graphic.png") 50% no-repeat;
  width: 47px;
  height: 61px;
}

.ba-services__item-icon-web {
  background: url("../img/icon-web.png") 50% no-repeat;
  width: 58px;
  height: 59px;
}

.ba-services__item-icon-seo {
  background: url("../img/icon-seo.png") 50% no-repeat;
  width: 50px;
  height: 61px;
}

.ba-services__item-icon-mobile {
  background: url("../img/icon-mobile.png") 50% no-repeat;
  width: 36px;
  height: 61px;
}

.ba-services__item-header {
  padding: 30px 0 20px;
  font-size: 24px;
  font-weight: 700;
  text-transform: uppercase;
}

.ba-services__item-text {
  font-style: italic;
}

.ba-services__item:hover {
  background-color: #33FF99;
  transform: scale(1.02);
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.8);
}

.ba-services__banner {
  max-width: 760px;
  flex-basis: 410px;
  flex-grow: 1;
  background: #9f9acf url("../img/service.png") 50% no-repeat;
  background-size: cover;
}

.ba-services__header {
  padding: 115px 10px 85px;
}

.ba-services__icon {
  background: url("../img/icon-services.png") 50% no-repeat;
  width: 62px;
  height: 60px;
}

.ba-services__description {
  text-align: center;
  color: #fff;
}

.ba-services__blockquote {
  font-style: italic;
}

.ba-services__blockquote-author {
  font-weight: 700;
  padding-bottom: 50px;
}

@media all and (max-width: 1215px) {
  .ba-services__banner {
    order: -1;
  }
}
